* {
  margin: 0;
  padding: 0;
}

body {
  color: #eaeaea;
  background-color: #222;
  font-family: Roboto Slab, serif;
}

select {
  max-width: 100%;
}

#root {
  grid-template-columns: auto 20rem;
  justify-content: center;
  gap: 2rem;
  margin: 2rem;
  display: grid;
}

.site-header {
  grid-column: 1 / 3;
}

.sidebar {
  flex-direction: column;
  gap: 1rem;
  display: flex;
}

.tool-selector, .seed-selector {
  grid-template-columns: repeat(auto-fit, minmax(3rem, auto));
  gap: 1rem;
  display: grid;
}

.tool-selector h2, .seed-selector h2 {
  grid-column: 1 / -1;
}

.tool, .seed {
  cursor: pointer;
  text-align: center;
  border: 1px solid gray;
  border-radius: .128rem;
  padding: 1rem;
  font-size: 1.5rem;
}

.tool.selected {
  color: #000;
  background-color: #fff;
}

.seed.selected {
  border-color: #fff;
}

.info {
  margin-block-start: auto;
}

